import { NextResponse } from 'next/server';

export async function GET() {
    try {
        // Streamable'dan video bilgilerini al
        const response = await fetch('https://api.streamable.com/videos/d0c9ib', {
            headers: {
                'Accept': 'application/json',
            },
        });

        if (!response.ok) {
            throw new Error('Video bilgisi alınamadı');
        }

        const data = await response.json();
        
        // MP4 URL'ini döndür
        if (data.files?.mp4?.url) {
            return NextResponse.json({ url: data.files.mp4.url });
        }

        throw new Error('Video URL bulunamadı');
    } catch (error) {
        console.error('Streamable video hatası:', error);
        return NextResponse.json(
            { error: 'Video yüklenemedi' },
            { status: 500 }
        );
    }
}
